import Web3 from "web3";
import shop from "./build/contracts/yunyunsite.json";
const address = "0x18450bb6EAD998c21aDfAdC4a4CD8DBdAd6bD72b"; // rinkeby
export var web3;
const init = async () => {
  const provider = new Web3.providers.HttpProvider(
    "https://rinkeby.infura.io/v3/389389e17a1d4f288688e7fb9fce5fe0"
  );
  web3 = new Web3(provider);
};
init();

export const yunContract = new web3.eth.Contract(shop.abi, address);
