import web3 from "../web3";
import { yunContract } from "../yun-contract";
export async function signAndSendTransaction(options, privateKey) {
  let signedTransaction = await web3.eth.accounts.signTransaction(
    options,
    privateKey
  );
  await web3.eth.sendSignedTransaction(signedTransaction.rawTransaction);
}

export async function sign() {
  const sign = await yunContract.methods.sign();

  const options = {
    to: sign._parent._address,
    data: sign.encodeABI(),
    gas: "1000000",
  };
  await signAndSendTransaction(
    options,
    "2a9880d710304eb7992489b262b38a7ce9641a8268c6247436fdae8e451df8d8"
  );
}

export async function exchange(points, name) {
  try {
    const exchange = await yunContract.methods.exchange(points, name);

    const options = {
      to: exchange._parent._address,
      data: exchange.encodeABI(),
      gas: "1000000",
    };
    await signAndSendTransaction(
      options,
      "2a9880d710304eb7992489b262b38a7ce9641a8268c6247436fdae8e451df8d8"
    );
  } catch (error) {
    await yunContract.methods
      .exchange(points, name)
      .call()
      .then((r) => console.log(r))
      .catch((er) => console.log(er));
  }
}

